.home-page {

    .text-zone {
        position: absolute;
        left: 13%;
        top: 50%;
        transform: translateY(-50%);
        width: 40%;
        max-height: 90%;
        white-space: nowrap;
    }
    h1 {
        color: #fff;
        font-size: 55px;
        margin: 0;
        font-family: 'Coolvetica';
        font-weight: 400;

        img {
            width: 32px;
            margin-left: 20px;
            opacity: 0;
            height: auto;
            animation: rotateIn 1s linear both;
            animation-delay: 1.4s;
        }
    }

    h2 {
        color: #8d8d8d;
        margin-top: 20px;
        font-weight: 400;
        font-size: 12px;
        font-family: 'sans-serif';
        letter-spacing: 2.5px;
        animation: fadeIn 1s 1.8s backwards;
    }

    .flat-button {
        background: #68e2fa;
        color: #333;
        font-size: 20px;
        font-weight: 400;
        text-decoration: none;
        letter-spacing: 1.5px;
        font-family: 'Coolvetica';
        padding: 15px 18px;
        border: 1px solid #68e2fa;
        margin-top: 25px;
        float: left;
        animation: fadeIn 1s 1,8s backwards;
        clip-path: polygon(0 0, 100% 0, 100% calc(100% - 8px), calc(100% - 8px) 100%, 0 100%);
        
        &:hover {
            transform: scale(1.1);
            transition-duration: 0.3s;
        }

        .i {
            padding-left: 10px;
        }
    }
}

@media screen and (max-width: 800px) {
    .home-page {
        .text-zone {
            position: initial;
            width: 100%;
            transform: none;
            padding: 10px;
            box-sizing: border-box;
            margin-top: 200px;
        }
    }
}

@media screen and (max-width: 500px) {
    .home-page {
        .text-zone {
            white-space: normal;

            h1 {
                font-size: 50px;
            }
        }
    }
}